
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import VideoUploader from "@/components/common/VideoUploader";
import Address from "@/components/common/Address";
import { SeourceCreatedSubmit } from "@/servers/api";
export default {
data() {
return {
inputValue: "",
textareaValue: "",
videoUrlData: "",
arressVal: ""
};
},
components: {
XTextarea,
Group,
XInput,
XButton,
VideoUploader,
Address
},
methods: {
getVideoAddress(val) {

this.arressVal = val;
},
getVideoUrl(val) {
this.videoUrlData = val;
},
onEvent(event) {

},
getInputVal() {

},
getTextareaValue() {

},
showToast(content) {
this.$vux.toast.text(content, "mid");
setTimeout(() => {
this.$vux.toast.hide();
}, 2000);
},
formData() {
if (!this.inputValue) {
this.showToast("请输入标题");
return;
} else if (!this.textareaValue) {
this.showToast("请输入内容");
return;
} else if (!this.videoUrlData) {
this.showToast("请上传视频");
return;
} else if (!this.arressVal) {
this.$vux.confirm.show({
title: "提示",
content: "发布地点未选，确定要发布吗？",
onCancel() {
return;
},
onConfirm: () => {
this.submitForm();
}
});
} else {
this.submitForm();
}
},
submitForm() {
this.$vux.loading.show({
text: "正在发布"
});
SeourceCreatedSubmit({
type: this.$route.query.branch,
title: this.inputValue,
summary: this.textareaValue,
video: this.videoUrlData,
address: this.arressVal
})
.then(res => {
console.log(res);
if (res.result === 1) {
this.$vux.loading.hide();
this.$vux.toast.show({
type: "success",
text: "发布成功",
time: 1000,
onHide: () => {
this.$router.goBack();
}
});
} else {
this.$vux.loading.hide();
this.$vux.toast.show({
type: "warn",
text: "失败，请重试",
time: 1000
});
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
}
}
};



